import React, { useEffect, useState,useRef, useLayoutEffect, useMemo } from 'react'
import { Input } from 'antd'

/** 解决useState参数来自于props，props更新，state不更新的问题
 * 使用ref保存状态，然后使用setState更新
 */

export default function S1() {
    const [v, setV] = useState('')

    return (
        <div>
            使用useEffect修改sate，从而造成2次更新 ： <br />
            <Input placeholder='修改传子组件的props' value={v} onChange={(e) => setV(e.target.value)}></Input> <br />
            ----------------下面是子组件：---------------------- <br />
            <ByUseRef a={v}></ByUseRef>
        </div>
    )
}



function a9999() {
    console.log('a9999')
}

let obj = {
    color: 'red'
}

let objProxy = new Proxy(obj, {
    get(...arg) {
        a9999();
        return Reflect.get(...arg)
    }
})

/**
 * 使用useRef保存状态，然后使用空的setState刷新组件
 */
function ByUseRef(props: any) {
    const stateRef = useRef(props.a)
    const [, update] = useState({})
    const updateState = (v:any) => {
        stateRef.current = v;
        update({}); // 使用空的useState刷新子组件
    }
   
/*     useLayoutEffect(() => {
       /// 因为useLayoutEffect的回调什么的是在ByUseRef组件performUnitOfWork之后的commitRoot阶段！
       /// 此时ByUseRef及其子组件属性已经在performUnitOfWork就更新完了！所以这么写的话，视图会滞后一次更新
        stateRef.current = props.a; 
        a8888();
    }, [props.a]) */

    console.log('1')
    useMemo(() => {  // useMemo的回调是同步的！
        stateRef.current = props.a;
        console.log('2')
    },[props.a])
    console.log('3')

    console.log('子组件更新', { 'props.a': props.a, 'stateRef.current': stateRef.current })

    return <div>
        子组件自己更新state <input value={stateRef.current} onChange={(e) => {
            updateState(e.target.value)
        }}></input>
        <code>
            stateRef.current:{stateRef.current}
            <div>
                <div style={{ color: objProxy.color }}>红色！ </div>
            </div>
        </code>
    </div>
}
