import React from 'react'
import styles from './styles.less'
import A1 from './demos/a1';
import A4 from './demos/a4';
import A5 from './demos/a5';

export default function PerformanceAbout() {
  return (
      <div>
          性能优化相关
          <a href="https://blog.csdn.net/zjjcchina/article/details/121469117">全面的</a>
          <div className={styles.container} >
              <h2>1. React.memo和componentShouldUpdate/React.PureComponent</h2>
              <p>这3个都是一样的道理，通过对比props对比，决定是否需要更新， <br />
              其中React.memo就是高阶组件，用它来包裹小组件即可
              </p>
              <code>
                  <pre>
{`                     function MyComponent(props) {
                          /* 使用 props 渲染 */
                      }
                      function areEqual(prevProps, nextProps) {
                          /*
                         如果前后的props一样 则可以return true表示不更新，
                          */
                      }
                      export default React.memo(MyComponent, areEqual);
`}
                  </pre>
              </code>
              <br />
              <A1></A1>
          </div>
          <br />
          <div className={styles.container} >
              <h2>2. 使用useMemo缓存大量的计算，useCallback</h2>
              <h3>
                  3.  使用React.Fragment避免添加额外的DOM，就是一个空标签
              </h3>
              <p></p>
              <code>
                  <pre>
                      {`                   
// 避免这样做
function Component(props) {
  const someProp = heavyCalculation(props.item);
  return <AnotherComponent someProp={someProp} /> 
}
  
// 只有 props.item 改变时someProp的值才会被重新计算
                      function Component(props) {
  const someProp = useMemo(() => heavyCalculation(props.item), [props.item]);
return <AnotherComponent someProp={someProp} /> 
}
`}
                  </pre>
              </code>
              <br />
              useCallbak和这个用法区别：useMemo的第一个参数的运行结果才是缓存的值； <br />
              而useCallback的第一个参数就是缓存的结果,useCallback的作用是：当一个子组件接受了一个函数作为props，然后父组件重渲染了，这个函数也就重新声明了，
              子组件就必定会触发更新，于是就使用useCallback，缓存这个函数，这样子父组件更新的时候就不会触发函数重新声明了;
          </div>

          <div className={styles.container}>
              <h2>4. 高阶组件</h2>
              <A4></A4>
          </div>
          <div className={styles.container}>
              <h2>5. useReducer替代useState</h2>
              <A5></A5>
          </div>
    </div>
  )
}
