import React, { JSXElementConstructor } from 'react'


interface Props {
    v: string;
    [p: string]: any
}

const Res = Happy<any>(Say)
export default function A4() {
    return (
        <div>
            高阶组件就是一个以组件为参数的函数，
            里面一般是一些抽出来的逻辑，然后将这段逻辑得到的结果作为prosp传递给入参组件；然后以某种形式返回它
            <br />
            例如：
            <br />
            <Res a='还可以注入另一个参数' />
        </div>
    )
}


interface MustKey {
    v: string;
    [p: string]: any
}


/** 这是一个高阶组件 */
function Happy<T>(Com: React.FC<any>) {
    let res = '现在是获取了happy' // 假设这是一段复杂逻辑的结果

    return (props:T) => {
        // 然后将复杂的逻辑的结果注入到入参组件中，并返回
        return <Com v={res} {...props}></Com>
    }
}

function Say(props: Props) {
    return <i>{props.v}，另一个：{props.a}</i>
}

