import { Input } from 'antd';
import React, { useState, useRef, useEffect } from 'react'
import { useSelector, useDispatch } from 'react-redux';
import { decrement, increment } from './count';
import { counterAdd } from './count2'; // 4. 把slice导出的actions导入，

export default function ReduxTest() {
    // 使用useDispatch去使用slice的actions去更新它的state
    const dispatch = useDispatch()
    const input = useRef<HTMLInputElement>(null)

    useEffect(() => {
        console.log(input.current, 'ref转发 ')
    }, [])

    // 使用useSelector去获取全局的state，
    const count = useSelector((state: any) => state.count2)
    console.log('store：',count)
    return (
        <div>
            <button onClick={() => {
                setTimeout(() => {
                    dispatch(counterAdd())  // 5. 然后使用dispactch调用actions，这样就可以修改slice的状态了，
                }, 2000);

            }}>点击全局cout+1</button> <br /><br />
            valueObj:{count?.valueObj?.v} <br />
            value:{count?.value} <br />
            v:{count?.v}
            <T2 v={count?.value}></T2>
            <MyInput ref={input}></MyInput>
        </div>
    )
}

function T2(props: { v: number }) {
    const clickDiv = useRef<HTMLDivElement>(null)
    const [c, setC] = useState('')
    useEffect(() => {
        if (clickDiv.current) {
            function click888(add: () => any) {
                add()
            }
            clickDiv.current.addEventListener('click', () => {
                click888(() => setC(Math.random().toString(16).slice(2, 8)))
                setTimeout(() => {
                    setC('第一次')
                    console.log('第一次：', c)
                }, 500);
            })
        }
    }, [])

    console.log('render C:', c)
    return <div>
        {props.v}
        <div>
            <div ref={clickDiv}>原生事件更新c:{c}</div>
            <T3 v={props.v}></T3>
            <div onClick={() => {
                setC(pre => {
                    console.log('pre1:', pre)
                    return pre + '*'
                })
                setC(pre => {
                    console.log('pre2:', pre)
                    return pre + '*'
                })
            }} >合成事件更新c:{c}</div>
        </div>
    </div>
}

function T3(props: { v: number }) {
    return <div>
        {props.v}
    </div>
}


const MyInput = React.forwardRef<HTMLInputElement | null, any>((props, myRef) => {
    return <div>
        <input ref={myRef} type="text" />
    </div>
})