import React,{useEffect, useState} from 'react'
import { Input } from 'antd'

/** 使用useEffect修改sate，从而造成2次更新，解决useState参数来自于props，props更新，state不更新的问题 */

export default function S2() {
    const [v,setV] = useState('')

  return (
      <div>
          使用useEffect修改sate，从而造成2次更新 ： <br />
          <Input placeholder='修改传子组件的props' value={v} onChange={(e) => setV(e.target.value)}></Input> <br />
          <ByEffect a={v}></ByEffect>
    </div>
  )
}


/**
 * 使用useEffect修改sate，从而造成2次更新
 */
function ByEffect(props:any) {
    const [state, setState] = useState(props.a)
    useEffect(() => {
        setState(props.a)
    }, [props.a])

    console.log('子组件渲染：',{state,'props.a':props.a})
    
    return <div>
        <code>
            state: {state} <br />
            props.a: {props.a}
        </code>
    </div>
}