import React, {useState} from 'react'

function getInitState(...arg: any) {
    console.log('经过复杂计算，得到了初始state：', arg ?? '')
    return getRandom()
}


export function getRandom() {
    return Math.random().toString(16).slice(2, 8)
}

/** setState 的demo */
export function InitState(props: any) {
    const [state1, setState1] = useState('');
    const [state2, setState2] = useState(getInitState('state2初始化'));// 错误的初始化方式，每次组件更新都会运行，但是值不会改变
    const [state3, setState3] = useState(() => getInitState('state3初始化'));// 
    const myState = useMyHooks(props.num1)
    const myState2 = useMyHooks2(props.num1)//BUG 由于这里面使用了setState来管理状态，会导致props.num1更新了，而myState2不会更新！
    return (
        <div style={{ border: '1px solid red', margin: '10px' }}>
            <div>
                <h2>state惰性初始化</h2>
                关键词： hooks 初始化 <br /> <br />
                <button onClick={() => setState1(getRandom())}>点击setState1：{state1}</button> <br />
                <h4>自定义 hooks状态- myState: {myState} <br /></h4>
                <h4>自定义 hooks状态- myState2: {myState2} <br /></h4>
                <div>
                    这里使用自定义hooks就有一个陷阱：由于自定义hooks使用了setState来初始化状态，会导致它和setState一样，只记住了第一次的值
                </div>
                <h4>hooks状态-state1: {state1} <br /></h4>
                <h4>hooks状态-state2: {state2} <br /></h4>
                <code>
                    useState(getInitState('state2'));
                </code> <br />
                是错误的惰性初始化方式：每次组件更新，getInitState都会运行，但是值并不会改变；这毫无意义； <br />
                除此之外，如果hooks的参数来自props也会如此
                <br />
                <h4>hooks状态-state3: {state3} <br /></h4>
                <code>
                    {`useState(() => getInitState('state3初始化'))`}
                </code> <br />
                这才是正确的惰性初始化方式
                <br /> <br />
            </div>
        </div>
    )
}

function useMyHooks(st: any) {
    return st
}

function useMyHooks2(st: any) {
    const [s, setS] = useState(st)
    return s
}