import React, { useCallback, useMemo, useReducer, useState } from 'react'

export default function A5() {
    return (
        <div>
            const [state, dispatch] = useReducer(reducer, initialState) <br />
            1. 首先useReducer返回的内容是一个state和disPatch，这个dispatch是和useCallback类似，已经是被缓存了的 <br /><br />
            2. 然后useReducer的第一个参数是一个函数，这个函数的返回值将作为下一个state；这个函数有2个参数：第一个参数是当前的state，第二个参数是dispatch传给它的;<br />
            useReducer第二个参数就是默认的state
            <br /><br />
            <b>可以用来处理一些比较复杂的state，或者state的更新依赖于旧的state</b>
            <br /> <br /><br />

            <Com></Com>
        </div>
    )
}

const initialState: any = {
    count: 0,
    v: '',
}
/** useReducer的第一个参数,是一个函数，它的返回值将会变成下一个state， */
function reducer(state: any, action: any) {
    console.log({ state, action })

    return {
        count: 0,
        v: JSON.stringify(action)
    }
}

const MyBtn = React.memo(function MyButton(props: any) { //1. useCallback必须结合React.memo使用
    console.log('自定义的按钮更新了')
    return <button onClick={()=>props.onClick('可选的传值')}>点击传值1</button>
}
)
function Com() {
    const [state, dispatchState] = useReducer(reducer, initialState) // dispatchState会被自动缓存，如同useCallback那样
    const memo = useMemo(()=>123,[])
    console.log('更新后的state：', state,memo)
    // 触发更新的state
    const [c, setC] = useState('')

    const btnClick = useCallback(() => dispatchState({ v: '传给reducer第二个参数的值' }), []) //2. 使用useCallback传给MyButton；

    return <div>
        <button onClick={() => { setC(Math.random().toString(16).slice(3, 9)) }}>点击更新本组件的setState的控制的状态{c}</button> <br />
        <MyBtn onClick={btnClick}></MyBtn>
        <MyBtn onClick={dispatchState}></MyBtn>
    </div>
}

