import React, { useState, useEffect } from 'react'

function T1() {
    const date = useDelayFetch('爱上了JFK')
    const context = useDate(date)

    return (
        <div>
            T1

            <br />
            这是渲染的数据： <br />
            {context}
        </div>
    )
}


/** 异步获取数据 */
function useDelayFetch(text: string) {
    const [date, setDate] = useState<any>()

    useEffect(() => {
        setTimeout(() => {
            setDate('已经获得随机数据：' + text + Math.random())
        }, 1000);
    }, [text])
    console.log('useDelayFetch,返回的数据:', date)

    return date
}

/** 根据数据来渲染处一段html */
function useDate(text: string='') {
    return <div style={{ border: '1px solid red' }}>
        <p style={{ color: 'red' }}>{text.split('.')[0]}</p>
        <p style={{ color: 'green' }}>{text.split('.')[1]}</p>
    </div>
}

export default React.memo(T1)