import React from 'react'
import styles from './styles.less'

export default function Safe() {
    return (
        <div>
            <h3>http请求 ，<a href="https://blog.csdn.net/m0_37145844/article/details/106917749">安全的请求等</a> </h3>
            <div className={styles.container}>
                <h3>1. 点劫持攻击</h3>
                <p>原理：假设我是一个恶意网站，做了一个按钮：点击经验加3；并且把弄个iframe，把csdn的关注按钮对准这个+3按钮，
                    那么当你打开恶意网站，就会加载这个iframe，由于自动登录，csdn会自动登录上，当你点击+3，实际点击的就是你登录后的csdn的按钮，
                </p>
                <p>防止的方法：
                    http头中加： <br />
                    X-FRAME-OPTIONS <br />

                    X-FRAME-OPTIONS是微软提出的一个http头，专门用来防御利用iframe嵌套的点击劫持攻击。并且在IE8、Firefox3.6、Chrome4以上的版本均能很好的支持。

                    这个头可以配置：<br />

                    DENY 拒绝任何域加载 <br />
                    SAMEORIGIN 允许同源域下加载 <br />
                    ALLOW-FROM 可以定义允许frame加载的页面地址 <br />
                </p>
                这样就可以禁止该网站在iframe中显示，比如百度：
                <iframe src="https://www.baidu.com" width={100} height={200}></iframe>
            </div>
            <div className={styles.container}>
                <h3>2. CSRF/XSRF 攻击</h3>
                <h4><a href="https://zh.javascript.info/cookie#samesite">sameSite有关于此</a>
                </h4>
                <h5>csrf原理：</h5>
                想象一下，你登录了 bank.com 网站。此时：你有了来自该网站的身份验证 cookie。你的浏览器会在每次请求时将其发送到 bank.com，以便识别你，并执行所有敏感的财务上的操作。

                现在，在另外一个窗口中浏览网页时，你不小心访问了另一个网站 evil.com。该网站具有向 bank.com 网站提交一个具有启动与黑客账户交易的字段的表单<br />{` <form action="https://bank.com/pay">`} 的 JavaScript 代码。

                你每次访问 bank.com 时，浏览器都会发送 cookie，即使该表单是从 evil.com 提交过来的。因此，银行会识别你的身份，并执行真实的付款。

                <h4>如何防范：</h4>
                <h5>使用cookie的sameSite属性，保存身份相关的字段，使非同一个url的网站发的请求不携带该身份cookie</h5>
                <pre>
                    {`SameSite 能够有下面三种值：

Strict 仅容许一方请求携带 Cookie，即浏览器将只发送相同站点请求的 Cookie，即当前网页 URL 与请求目标 URL 彻底一致。
Lax 仅这种情况可以发送：是get/head请求，并且该操作执行顶级导航（更改浏览器地址栏中的 URL，如iframe和js请求就不是顶级导航栏）
None 不管是否跨站都会发送 Cookie

以前默认是 None 的，Chrome80 后默认是 Lax。`}
                    <a href="https://blog.csdn.net/weixin_38289101/article/details/127977426"> 原文链接：</a>
                </pre>
                <br />
                <br />
                <h5>csrf Token</h5>
                浏览器发请求只是会携带cookie，第三方是看不到的，所以后端直接通过set-cookie设置一个token，
，然后前端将token放到url的参数里，后端对比cookie中的token和url中的token即可
            </div>
            
            <div className={styles.container}>
                <h3>xss攻击</h3>
                原理：用户输入的内容被当作html/js代码执行了； <br />
                就前端来说，react会将用户的输入自动转义成字符串，所以不用担心用户会直接注入html，但是react中有一些写法，可能会将用户的输入当作html执行；
                比如使用innterhtml相关的api插入用户的输入到代码中；
                <br />
                或者使用用户的输入直接作为某些标签的url，但是他却使用了javascript:开头，这会导致js将这个url当作js执行；
                关于这种形式的攻击，不仅前端需要过滤，并且后端也需要过滤，因为用户是可以通过本地替换项目代码，进行修改验证逻辑，从而绕过前端的验证的；
            </div>
        </div>
    )
}
class A {
     
}
 
class B {

}

class C extends A{
    
}