import React, { useState } from 'react'
import { Input } from 'antd'

export default function A1() {

  const [v, setV] = useState(0)
  return (
    <div>
      <Input onChange={(e) => { setV(+e.target.value) }} type="number" />
      通过自定义的比较函数，控制输入值为单数的时候才更新子组件: <br />
      子组件：<MemoSon v={v}></MemoSon>
    </div>
  )
}


const MemoSon = React.memo(Com, (pre, next) => {
  // 可以像componentShouldUpdate一样自定义比较函数，return true表示不更新，
  console.log(pre,next,'****')
  if (next.v % 2 === 1) {
    return false
  }

  return true
})

function Com(props: any) {
  return <div>
    被memo包裹的组件{props.v}
  </div>
}