import res from 'antd-mobile-icons/es/AaOutline'
import React, { useState, useEffect, useLayoutEffect, useRef } from 'react'
import styles from './styles.less'

export default function Effects() {
    const container = useRef<HTMLDivElement>(null)
    const [count, setCount] = useState(0)

    useEffect(() => {
        effect111111_useEffect();
        changeContainerHeight();
    }, [count])

    useLayoutEffect(() => {
        effect111111_useLayoutEffect();
        changeContainerHeight();
    }, [count])

    return (
        <div>useEffect和useLayoutEffectbr
            <br />
            000000
            <div id='container'
                onClick={() => {
                    setTimeout(() => {
                        setCount(count + 1)
                    }, 1000);
                }}
                className={styles.container}
                ref={container}>
                点击修改容器样式，引起重排和重绘
            </div>
            0000000，
            结论：当前任务时间过长的话，会将useEffect的回调函数放到下一个任务去执行,useLayoutEffect的回调永远属于当前任务循环
        </div>
    )
}


function effect111111_useEffect() {
    console.log('useEffect执行')
    new Promise((res) => {
        res(1)
    }).then(() => { effect111111111111__useeffect_promise() })
}

function effect111111111111__useeffect_promise() {
    console.log(1)
}

function effect111111_useLayoutEffect() {
    delay()
    console.log('uselayout effect执行')
    new Promise((res) => {
        res(1)
    }).then(() => { effect2222222__useLayoutEffect_promise() })
}

function effect2222222__useLayoutEffect_promise() {
    console.log(1)
}

/**
 * 用来耗时的长循环
 */
function delay() {
    let v = 1
    for (let index = 0; index < 1e7; index++) {
        v = v + (-1) ** index
    }
    console.log(v)
}

function changeContainerHeight() {
    let container = document.getElementById('container')
    if (container) {
        let height = getComputedStyle(container).height
        container.style.height = parseInt(height) + 10 + 'px';
        height = getComputedStyle(container).height
        container.style.height = parseInt(height) + 5 + 'px';
    }
}

