import React, { useState, useEffect, useLayoutEffect, useCallback } from 'react'
import { Input } from 'antd'
import T1 from './components/t1'

export default function MyHooks() {
    const [changeCount, v] = useCount(10)
    const [input, setInput] = useState(0)

    console.log('主函数render，倒计时：',v)

    return (
        <div>
            <input onChange={(v => { setInput(+v.target.value) })} type="number" />
            <button onClick={() => { changeCount(input) }}>
                <div>使用这个数字开始倒数，当前倒数：{v}</div>
            </button>
            <div>
                1
            </div>
            ******* <br />
            <CountBtn text='点击倒数' onClick={() => console.log('点击了按钮')}></CountBtn>
            <div className='container'>
                <T1 />

            </div>
        </div>
    )
}


/** 从指定数字，倒计时至0 */
function useCount(defaultValue: number): [v: (start: number) => void, v2: number] {
    const [v, setV] = useState(defaultValue)


    const reset = (start: number) => {
        setV(start)
    }

    useEffect(() => {
        if (v > 0) {
            setTimeout(() => {
                setV(v - 1)
            }, 1000);
        }

    }, [v])

    return [reset, v]
}

// 1.
/** 按钮抛出：
 * 1. 按钮是否可以点击，
 * 2. 按钮的文案，
 * 3. 按钮的点击事件 */
function MyBtn(props: any) {
    return <div onClick={props.disable ? () => { } : props.onClick} style={{ border: '1px solid red' }}>
        组件是否可以点击: {String(!props.disable)} <br />
        组件文案：{props.text}
    </div>
}



// 2.
/**
 * 
 * 高阶组件把hooks和btn组合起来，这样就得到了一个
 */
function CountBtn(props: any) {
    const [setV, v] = useCount(0)
    const propsClick = props.onClick
    const btnClick = useCallback(() => {
        propsClick()
        setV(60)
    }, [propsClick, setV])

    return <div>
        <MyBtn onClick={btnClick}
            text={props.text + `(${v})`}
            disable={props.disable === undefined ? v !== 0 : v !== 0 && props.disable}
        ></MyBtn>
    </div>
}
