import React, { useState, useReducer } from 'react'
import { Col, Collapse } from 'antd'
import { InitState, getRandom } from './components/init'
import S1 from './components/S1'
import S2 from './components/S2'

export default function RecucerTest() {
    const [num1, setNum1] = useState('')

    return <div>
        <div className="container">
            <h2>useState注意事项</h2>

            <Collapse>
                <Collapse.Panel header='useState初始化的时候注意事项' key={'1'}>
                    <div>
                        <div>
                            <ul>
                                <li>
                                    <b>每次函数更新，其内的hooks都会运行一遍，所以useState也会运行一次，但是useState会记住最初传给它的值，并不会随着参数的改变而改变,
                                    </b>
                                </li>
                                <li>
                                    <h4>惰性初始化：传入一个函数</h4>
                                    <p className='desc'>
                                        正确的： 比如<code>{`const [state,setState] = useState( Math.random )`}</code>  <br />
                                        那么state将会得到一个随机数，可以用这种方式来计算复杂的值 ;即使组件多次更新，Math.random只会在初始化的时候调用1次！ <br /><br />
                                        <b>错误的</b>： 比如 {`const [state2,setState2] = useState( Math.random() )`} <br />
                                        每次组件更新都会重新调用里面的Math.random()，
                                        但是state2的值并不会改变，它会记住第一次的Math.random()；
                                        <br />
                                        但是自定义hooks不会有这个问题
                                    </p>
                                </li>
                                <li>
                                    <h4>传入每次组件渲染都会改变的值，如props</h4>
                                    <p className='desc'>
                                        比如 {`const [state,setState] = useState( props.a )`} <br />
                                        如果props传过来的a会改变，那么它将只会记住最开始的那个a ! ；
                                        这种情况和上面的错误情况是一样的 <br />
                                        <br />
                                        但是自定义hooks不会有这个问题， <br />
                                        比如 {`const [s,setS] = useMy( props.a )`} <br />
                                        它会随着props.a的更新而更新，除非自定义hooks返回的s实际上是由useState生成的state
                                    </p>
                                </li>
                            </ul>
                        </div>
                        <div>
                            <Collapse>
                                <Collapse.Panel key={'1'} header='useState初始化demo'>
                                    <div className='container'>
                                        <button onClick={() => setNum1(getRandom())}>点击修改父组件的state - num1: {num1} </button>
                                        <InitState num1={num1} /> <br />
                                    </div>
                                </Collapse.Panel>
                                <h2>使用props作为hooks入参的 解决方案</h2>
                                <b>
                                    多数hooks都使用了useState作为状态管理，同时也把它的入参也来自于props，这个时候，如果传给hooks的入参改变了，那么其内使用的state并不会改变，从而导致该hooks返回的结果不会改变
                                </b>
                                <Collapse.Panel header="使用useEffect，但是会 2 次渲染" key={'2'}>
                                    这个方案只适用于useState，或者暴露了setState的自定义hooks，由于不一定暴露出了setState，所以无法在useEffect里面使用setState
                                    <br /> <br />
                                    <S1></S1>
                                </Collapse.Panel>
                                <Collapse.Panel header="使用useRef和空的setState刷新子组件" key={'3'}>
                                    看起来还好
                                    <br /> <br />
                                    <S2></S2>
                                </Collapse.Panel>

                                <Collapse.Panel header="使用useImperativeHandle结合forwardRef" key={'3'}>
                                    TODO
                                    不传a过去，子组件使用useImperativeHandle将useState转发给父组件，并使用memo包裹子组件
                                </Collapse.Panel>
                                <Collapse.Panel header="给子组件加key=a" key={'3'}>
                                    TODO
                                    这样每次由于key不同，会直接删掉子组件，重新弄一个子组件。
                                    如果是其他hooks，又没有抛出setState，就可以这样使用！
                                </Collapse.Panel>
                            </Collapse>

                        </div>

                    </div>

                </Collapse.Panel>
            </Collapse>



        </div>

    </div>
}


